.form-box-login{

    position: relative;

    width: 320px;

    height: 350px;

    background: transparent;

    border:none;

    border-radius: 20px;

    -webkit-backdrop-filter:blur(15px) brightness(80%);

            backdrop-filter:blur(15px) brightness(80%);

    

    display: flex;

    justify-content: center;

    align-items: center;

}

.login_head{

    font-size: 2em;

    color:#fff;

    text-align: center;

}

.inputbox{

    position: relative;

    margin: 30px 0;

    width: 270px;

    border-bottom: 2px solid #fff;

}

.inputbox label{

    position: absolute;

    top:50%;

    left: 5px;

    -webkit-transform: translateY(-50%);

            transform: translateY(-50%);

    color: #fff;

    font-size: 1em;

    pointer-events:none;

    transition: .5s;

}

/* animations: start */

input:focus ~label,

input:valid ~label{

    top: -5px;

}

/* animation:end */

.inputbox input{

    width: 100%;

    height: 50px;

    background: transparent;

    border: none;

    outline: none;

    font-size: 1em;

    padding: 0 35px 0 5px;

    color: #fff;

}

.inputbox ion-icon{

    position: absolute;

    right:8px;

    color: #fff;

    font-size: 1.2em;

    top:20px;

}



.admin_button{

    width: 100%;

    height: 40px;

    border-radius: 40px;

    background-color: #fff;

    border:none;
    color: black;
    outline: none;

    cursor: pointer;

    font-size: 1em;

    font-weight: 600;

}


